import mButton from '../../../../components/m-button.vue'
import tkiQrcode from '@/components/tki-qrcode/tki-qrcode.vue'
export default {
	data() {
		return {
			value: false,
			options: [{
				value: 1,
				label: "我同意"
			}],
			checkes: true,
			checked: false,
			ifShow: true,
			val: '', // 要生成的二维码值
			size: 490, // 二维码大小
			unit: 'upx', // 单位
			background: '#b4e9e2', // 背景色
			foreground: '#309286', // 前景色
			pdground: '#32dbc6', // 角标色
			icon: '', // 二维码图标
			iconsize: 40, // 二维码图标大小
			lv: 3, // 二维码容错级别 ， 一般不用设置，默认就行
			onval: true, // val值变化时自动重新生成二维码
			loadMake: true, // 组件加载完成后自动生成二维码
			src: '', // 二维码生成后的图片地址或base64
			id: '',
			no: '',
			otherNumber: '' //他人号码
		}
	},
	onLoad(option) {
		this.id = option.id;
		this.no = option.no;
		this.getCode();
	},
	methods: {
		getCode() {
			this.test.post('order/reserve_encrypt', {
				id: this.id,
				no: this.no,
			}).then(res => {
				console.log(res)
				if (res.statusCode == 200 && res.data.errorCode == 0) {
					this.val = res.data.value
				} else {
					uni.showModal({
						title: '提示',
						content: res.data.message,
						success: function(res) {
							if (res.confirm) {
								uni.reLaunch({
									url: '../../../login/login'
								})
							} else {
								uni.reLaunch({
									url: '../../../login/login'
								})
							}
						}
					})
				}
			}).catch(err => {
				console.log(err)
			})
		},
		cancel() {
			this.checked = !this.checked;
			this.checkes = !this.checkes
		},
		qrR(res) {
			this.src = res
		},
		exemption(){
			uni.navigateTo({
				url:'../exemption/exemption'
			})
		},
		send() {

			if (this.checkes == true) {
				// Toast("请阅读免责条款,勾选后方可发送");
				return uni.showToast({
					title: "请阅读免责条款,勾选后方可发送",
					"icon": "none"
				})
			} else {
				if (this.otherNumber !== '') {
					if (!/^1[3456789]\d{9}$/.test(this.otherNumber)) {
						return uni.showToast({
							"title": '请填写正确的手机号码',
							"icon": "none"
						})
					} else {
						this.test.post('order/send_get_encrypt', {
							phone: this.otherNumber,
							encrypt_sn: this.val
						}).then(res => {
							console.log(res)
							if (res.statusCode == 200 && res.data.errorCode == 0) {
								uni.showToast({
									title: '发送成功'
								})
							}else{
								uni.showToast({
									title:res.data.message,
									icon:'none'
								})
							}
						}).catch(err => {
							console.log(err)
						})

					}
				} else {
					return uni.showToast({
						title: "请填写手机号码",
						"icon": "none"
					})
				}

			}


		}
	},
	components: {
		mButton,
		tkiQrcode
	},
}
